import React, { useContext, useEffect, useState } from 'react';
import { ContextFavoritos } from '../../contexto/ContextFavoritos';
import { ContextTema } from '../../contexto/ContextTema';
import './favoritos.css'

function Favoritos() {
    const [favoritos, setFavoritos] = useState([])
    const [comparacao, setComparacao] = useState(['', ''])
    const addListaFavoritos = useContext(ContextFavoritos)
    const contextoTema = useContext(ContextTema)
    addListaFavoritos.funcao = addFavoritos

    let tituloFavoritos = ''
    let btnComparar = ''
    let btnLimpar = ''
    let sombra = ''
    alterarCor()
    getFavoritos()
    getComparacao()

    function addFavoritos(dados) {
        if (!verificarSeExisteFavoritos(dados)) {
            adicionarNoFavoritos(dados)
        } else {
            removerDosFavoritos(dados)
        }
    }

    function alterarCor() {
        if (contextoTema.tema) {
            tituloFavoritos = 'favoritos-titulo-lista letraEscura'
            btnComparar = 'favoritos-btn comparar sombraBtn'
            btnLimpar = 'favoritos-btn limparFavoritos sombraBtn'
            sombra = 'favoritos-item sombraEscura'
        } else {
            tituloFavoritos = 'favoritos-titulo-lista'
            btnComparar = 'favoritos-btn comparar'
            btnLimpar = 'favoritos-btn limparFavoritos'
            sombra = 'favoritos-item'
        }
    }

    function getFavoritos() {
        const listaFavoritos = JSON.parse(localStorage.getItem('favoritos')) || []
        if (JSON.stringify(listaFavoritos) !== JSON.stringify(favoritos)) {
            setFavoritos(listaFavoritos)
        }
    }

    function getComparacao() {
        const listaComparacao = JSON.parse(localStorage.getItem('listaComparacao')) || ['', '']
        if (JSON.stringify(listaComparacao) !== JSON.stringify(comparacao)) {
            setComparacao(listaComparacao)
        }
    }

    function removerDosFavoritos(dados) {
        const novoFavoritos = favoritos
        novoFavoritos.forEach((item, index) => {
            if (JSON.stringify(item) === JSON.stringify(dados)) {
                novoFavoritos.splice(index, 1)
            }
        })
        localStorage.setItem('favoritos', JSON.stringify(novoFavoritos))
        setFavoritos([...novoFavoritos])
    }

    function adicionarNoFavoritos(dados) {
        const novoFavoritos = favoritos
        novoFavoritos.push(dados)
        localStorage.setItem('favoritos', JSON.stringify(novoFavoritos))
        setFavoritos([...novoFavoritos])
    }

    function verificarSeExisteFavoritos(dados) {
        let jaExiste = false
        favoritos.forEach(item => {
            if (JSON.stringify(item) === JSON.stringify(dados)) {
                jaExiste = true
            }
        })
        return jaExiste;
    }
    function addComparacao(event) { 
        const li = event.target
        const info = { codigo: li.getAttribute('data-codigo'), ano: li.getAttribute('data-anomodelo') }
        verificaComparacao(info, li)
    }

    function verificaComparacao(info, li) {
        if (!adicionarItemComparacao(info, li)) {
            removerItemComparacaoIgual(info, li)
        }
    }

    function adicionarItemComparacao(info, li) {
        let veiculo1 = comparacao[0]
        let veiculo2 = comparacao[1]
        let adicionado = false
        if (veiculo1 === '' && JSON.stringify(veiculo2) !== JSON.stringify(info)) {
            veiculo1 = info
            adicionado = true
            adicionarFundo(li)
        } else if (veiculo2 === '' && JSON.stringify(veiculo1) !== JSON.stringify(info)) {
            veiculo2 = info
            adicionado = true
            adicionarFundo(li)
        }

        localStorage.setItem('listaComparacao', JSON.stringify([veiculo1, veiculo2]))
        setComparacao([veiculo1, veiculo2])
        return adicionado
    }

    function removerItemComparacaoIgual(info, li) {
        let veiculo1 = comparacao[0]
        let veiculo2 = comparacao[1]
        if (JSON.stringify(veiculo1) === JSON.stringify(info)) {
            veiculo1 = ''
            removerFundo(li)
        }
        if (JSON.stringify(veiculo2) === JSON.stringify(info)) {
            veiculo2 = ''
            removerFundo(li)
        }
        localStorage.setItem('listaComparacao', JSON.stringify([veiculo1, veiculo2]))
        setComparacao([veiculo1, veiculo2])
    }

    function adicionarFundo(li) {
        li.classList.add('active')
    }
    function removerFundo(li) {
        li.classList.remove('active')
    }

    useEffect(() => {
        const itens = document.querySelectorAll('[data-codigo]')
        itens.forEach(item => {
            const obj = {codigo: item.getAttribute('data-codigo'), ano: item.getAttribute('data-anomodelo')}
            comparacao.forEach(comparacao => {
                if(JSON.stringify(comparacao) === JSON.stringify(obj)) {
                    item.classList.add('active')
                }
            })
        })
    },[comparacao])

    return (
        <div className="container conteudo">
            <div className="favoritos">
                <h2 className={tituloFavoritos}>Lista de Favoritos</h2>
                <div className="favoritos-div">
                    <ul className="favoritos-lista" data-listafavoritos>
                        {
                            favoritos.map((item, index) => {
                                return <li name="itemfavorito" key={index} className={sombra} data-codigo={item.CodigoFipe} data-anomodelo={item.AnoModelo} onClick={addComparacao}>{item.Modelo} - {item.AnoModelo}</li>
                            })
                        }
                    </ul>
                </div>
                <div className="favoritos-botoes">
                    <button className={btnComparar}>Comparar</button>
                    <button className={btnLimpar}>Limpar Favoritos</button>
                </div>
            </div>
        </div>
    );
}

export default Favoritos;